.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.OptionSign {
  display: inline-block;
  background-color: #fff;
  text-align: center;
  padding: 10px;
  width: 128px;
  margin: 0.5em;
  border-radius: 5%;
  color: black;
}

.ChallengeType {
  border-radius: 5px;
  border-color: gray;
  bottom: 0.25em;
  margin-left: 1em;
  padding: 7px;
}
